import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  ModalFooter,
  FormControl,
  FormLabel,
  Input,
  Button,
} from "@chakra-ui/react";

const EditPlayerModal = ({
  isOpen,
  onClose,
  editedPlayerName,
  setEditedPlayerName,
  saveEditedPlayerName,
}) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader> </ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <FormControl>
            <FormLabel> </FormLabel>
            <Input
              type="text"
              value={editedPlayerName}
              onChange={(e) => setEditedPlayerName(e.target.value)}
              color="white"
            />
          </FormControl>
        </ModalBody>
        <ModalFooter>
          <Button colorScheme="blue" mr={3} onClick={saveEditedPlayerName}>
             
          </Button>
          <Button variant="ghost" onClick={onClose}>
            
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

export default EditPlayerModal;
